.nav-bar {
  background-color: #21222a;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo img {
  width: 60px;
  height: 60px;
}

.logo h2 {
  color: #61dafb;
}

.nav-bar .logo {
  display: flex;
  align-items: center;
}

.logo-2 {
  color: white;
  margin-right: 20px;
}

h1 {
  font-size: 55px;
  color: white;
  margin-left: 30px;
  margin-top: 10px;
}

.ul {
  color: white;
  padding: 30px;
  margin-left: 40px;
  list-style: none;
}

ul li {
  margin: 15px 0px;
  font-size: 19px;
  color: #ffffff;
}

span {
  background-color: #61dafb;
  width: 10px;
  height: 10px;
  margin-right: 1rem;
  transform: translateY(-50%);
  transform: translateX(-50%);
  border-radius: 50%;
}

.li-cont {
  display: flex;
  align-items: center;
}
